import Vue from 'vue'
import Router from 'vue-router'
import USDI from './views/USDI.vue'
import Charts from './views/Charts.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'charts',
      component: Charts
    },
    {
      path: '/usdi',
      name: 'USDI',
      component:USDI
    }
  ]
})
