      Upload: function () {
        var user_name = window.prompt("Input your name", "");
        var data_name = window.prompt("Input data name", "");
        var mail_address = window.prompt("Input your e-mail address", "");
        var today = new Date();
        var y = today.getFullYear();
        // JavaScript months are 0-based.
        var m = today.getMonth() + 1;
        var d = today.getDate();
        var h = today.getHours();
        var mi = today.getMinutes();
        var uploaded_time = y + "-" + m + "-" + d + "-" + h + "-" + mi;

        if (user_name === '' || mail_address === '') {
          alert('OK was pressed with the input field empty');
        } else if (user_name === null || user_name === null) {
          alert('Cancel has been pressed');
        } else if (checkEmailAddress(mail_address) == false) {
          alert('Please enter a valid email address format.');
        } else {
          var Key = Math.random().toString(32).substring(2);
